/**
* NEUTRAL - Placeholder Utilities          
**/

%db-bg-neutral {
    background-color: $db-colors-neutral-enabled;
    color: $db-colors-neutral-on-enabled;
    
    &-ia,
    &[data-variant="ia"] {
        @extend %db-bg-neutral;
        
        &:hover {
            background-color: $db-colors-neutral-hover;
        }

        &:active {
            background-color: $db-colors-neutral-pressed;
        }
        
    }

    &-text-ia,
    &[data-variant="text-ia"] {
       color: $db-colors-neutral-on-enabled;
       
        
        &:hover {
            color: $db-colors-neutral-on-hover;
        }

        &:active {
            color: $db-colors-neutral-on-pressed;
        }
        
    }
}
